<template>
	<view>
		<view class="head">
			<view class="touxang">头像</view>
			<view class="tximg">
				<image src="../../static/logo.png"></image>
			</view>
		</view>
		<view class="fenge"></view>
		<view class="con">
			<view class="display">
				<view>真实姓名</view>
				<view class="color">{{xm}}</view>
			</view>
			<view class="display">
				<view>性别</view>
				<view class="color">
					<label class="radio">
						<radio value="r1" style="transform:scale(0.7)" checked="true" color="#3DCCB4" />男</label>
					<label class="radio">
						<radio value="r2" style="transform:scale(0.7)" color="#3DCCB4" />女</label>
				</view>
			</view>
			<view class="display">
				<view>出生年月日</view>
				<view class="uni-list color">
					<view class="uni-list-cell">
						<view class="uni-list-cell-db">
							<picker mode="date" :value="date" :start="startDate" :end="endDate" @change="bindDateChange">
								<view class="uni-input">{{date}}</view>
							</picker>
						</view>
					</view>
				</view>
			</view>
			<view class="display">
				<view>初次发病时间</view>
				<view class="uni-list color">
					<view class="uni-list-cell">
						<view class="uni-list-cell-db">
							<picker mode="date" :value="date" :start="startDate" :end="endDate" @change="bindDateChange">
								<view class="uni-input">{{date}}</view>
							</picker>
						</view>
					</view>
				</view>
			</view>

			<view class="display">
				<view>现居住地</view>
				<view class="uni-list color">
					<picker mode="multiSelector" :range="cityList" :value="cityIndex" @change="bindCityChange">            
						 <view>
							 {{cityList[0][cityIndex[0]]}}->{{cityList[1][cityIndex[1]]}}
						</view>
					</picker>

				</view>
			</view>

			<view class="display">
				<view>确诊医院</view>
				<view>
					<input type="text" value="" placeholder="请输入确诊医院" />
				</view>
			</view>
			
			<view class="display">
				<view>初次发病时间</view>
				<view class="uni-list color">
					<view class="uni-list-cell">
						<view class="uni-list-cell-db">
							<picker mode="date" :value="date" :start="startDate" :end="endDate" @change="bindDateChange">
								<view class="uni-input">{{date}}</view>
							</picker>
						</view>
					</view>
				</view>
			</view>
		</view>
		<view>
			<button type="default" class="wcbtn" @click="wanchen()">完成</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			const currentDate = this.getDate({
				format: true
			})
			return {
				title: 'picker',
				date: currentDate,
				cityList: [
					['请选择城市', '洛阳', '北京', '广州', '上海', '深圳'],
					['请选择地区', '涧西区', '洛龙区', '西工区', '汝阳县']
				],
				cityIndex: [0, 0]
			}
		},
		computed: {
			startDate() {
				return this.getDate('start');
			},
			bindCityChange(e) {
				console.log('picker发送选择改变，携带值为', e.target.value)
				this.cityIndex = e.target.value
			},
			endDate() {
				return this.getDate('end');
			}
		},
		methods: {
			wanchen:function(){
				uni.showModal({
				    title: '提示',
				    content: '为了确保您的账户安全，${{app名称}}建议您完成手机号绑定',
				    success: function (res) {
				        if (res.confirm) {
				            console.log('用户点击确定');
							uni.navigateTo({
								url:'../bindingPhonePopup/bindingPhonePopup'
							})
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
				
			},
			bindDateChange: function(e) {
				this.date = e.target.value
			},
			getDate(type) {
				const date = new Date();
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();

				if (type === 'start') {
					year = year - 60;
				} else if (type === 'end') {
					year = year + 2;
				}
				month = month > 9 ? month : '0' + month;;
				day = day > 9 ? day : '0' + day;
				return `${year}-${month}-${day}`;
			}
		}
	}
</script>

<style scoped>
	.head {
		/* border-bottom: 1rpx solid #CACCCC; */
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 110px;
		font-size: 16px;
	}

	.head view {
		margin: 0 30px 0 30px;
	}

	.head>view image {
		width: 65px;
		height: 65px;
		border-radius: 12px;
	}

	.fenge {
		height: 25px;
		background-color: #f2f5f4;
	}

	.display {
		height: 45px;
		font-size: 15px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-left: 25px;
		padding-right: 25px;
	}

	.con>view:nth-of-type(1) {
		border-bottom: 6px solid #F2F5F4;
	}

	.con>view:nth-of-type(2) {
		border-bottom: 1px solid #F2F5F4;
	}
	.con>view:nth-of-type(4),.con>view:nth-of-type(5),.con>view:nth-of-type(6) {
		border-bottom: 1px solid #F2F5F4;
	}
	.color {
		color: #939998;
	}
	.display input{
		text-align: right;
	}
	.wcbtn{
		position: absolute;
		bottom: 140rpx;
		width: 600rpx;
		background-color: #3DCCB4;
		color: #fff;
		left: 50%;
		transform: translate(-50%);
	}
	.wcbtn::after{
		border: none;
	}
</style>
